import React from "react";
import "./SDButton.css";
import 'semantic-ui-css/semantic.min.css'
import { Image, Icon, Label } from 'semantic-ui-react'



const SDButton = props => {

    const bgColorStyles = {
        backgroundColor: props.bgColor
    };

    return (
        <div className="SDButton">
            <div className="SDButton-border-wrap">


                <div className="SDButton-conainer">


                    <div className="SDButton-content" style={bgColorStyles}>

                      {props.imageName && (
                        <Image className="imageContainer" src={props.imageName} size='small' />
                        )}

                        <div className="reflect-top-left"></div>
                        <div className="reflect-top"></div>
                        <div className="reflect-top-right"></div>

                        {props.playlistName && (
                        <Label className="labelContainer" >{props.playlistName}</Label>
                        )}

                        <div className="reflect-bottom-left"></div>
                        <div className="reflect-bottom"></div>
                        <div className="reflect-bottom-right"></div>



                        {props.iconName && (
                            <div className="iconContainerSmall">
                                <Icon className="iconCustomSmall" name={props.iconName} size='big' />
                            </div>
                        )}
                        {props.iconName && (
                            <div className="iconContainer">
                                <Icon className="iconCustom" name={props.iconName} size='big' />
                            </div>
                        )}



                    </div>


                </div>


            </div>
        </div>
    );
}

export default SDButton;
